import React, { createContext, useContext, useEffect, useState } from 'react';
import { storage } from '../utils/storage';
// Web-compatible alert - using browser's built-in alert for now
const Alert = {
  alert: (title: string, message?: string) => {
    if (message) {
      alert(`${title}: ${message}`);
    } else {
      alert(title);
    }
  }
};
import { authService } from '../services/authService';
import { User } from '../types/User';

interface AuthContextType {
  user: User | null;
  isAuthenticated: boolean;
  isLoading: boolean;
  loading: boolean; // Alias for isLoading for backward compatibility
  login: (email: string, password: string) => Promise<void>;
  register: (userData: RegisterData) => Promise<void>;
  logout: () => Promise<void>;
  updateUser: (userData: Partial<User>) => Promise<void>;
  enableBiometric: () => Promise<void>;
  biometricLogin: () => Promise<void>;
  isBiometricEnabled: boolean;
}

interface RegisterData {
  firstName: string;
  lastName: string;
  email: string;
  phoneNumber: string;
  ssn?: string;
  password: string;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [user, setUser] = useState<User | null>(null);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoading, setIsLoading] = useState(true);
  const [isBiometricEnabled, setIsBiometricEnabled] = useState(false);

  useEffect(() => {
    checkAuthStatus();
  }, []);

  const checkAuthStatus = async () => {
    try {
        const token = await storage.getItem('authToken');
      const userData = await storage.getItem('userData');
      const biometricEnabled = await storage.getItem('biometricEnabled');

      if (token && userData) {
        setUser(JSON.parse(userData));
        setIsAuthenticated(true);
        setIsBiometricEnabled(biometricEnabled === 'true');
      }
    } catch (error) {
      console.error('Error checking auth status:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const login = async (email: string, password: string) => {
    try {
      setIsLoading(true);
      const response = await authService.login(email, password);
      
      if (response.success && response.data) {
        const { token, user: userData } = response.data;
        
        await storage.setItem('authToken', token);
        await storage.setItem('userData', JSON.stringify(userData));
        
        setUser(userData);
        setIsAuthenticated(true);
      } else {
        throw new Error(response.error || 'Login failed');
      }
    } catch (error) {
      console.error('Login error:', error);
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  const register = async (userData: RegisterData) => {
    try {
      setIsLoading(true);
      const response = await authService.register(userData);
      
      if (response.success && response.data) {
        const { token, user: newUser } = response.data;
        
        await storage.setItem('authToken', token);
        await storage.setItem('userData', JSON.stringify(newUser));
        
        setUser(newUser);
        setIsAuthenticated(true);
      } else {
        throw new Error(response.error || 'Registration failed');
      }
    } catch (error) {
      console.error('Registration error:', error);
      throw error;
    } finally {
      setIsLoading(false);
    }
  };

  const logout = async () => {
    try {
      await storage.removeItem('authToken');
      await storage.removeItem('userData');
      
      setUser(null);
      setIsAuthenticated(false);
      setIsBiometricEnabled(false);
    } catch (error) {
      console.error('Logout error:', error);
    }
  };

  const enableBiometric = async () => {
    try {
      await storage.setItem('biometricEnabled', 'true');
      setIsBiometricEnabled(true);
      Alert.alert('Success', 'Biometric authentication enabled (mock)');
    } catch (error) {
      console.error('Error enabling biometric:', error);
      Alert.alert('Error', 'Failed to enable biometric authentication');
    }
  };

  const biometricLogin = async () => {
    try {
      Alert.alert('Biometric Login', 'Biometric login not implemented in this demo');
    } catch (error) {
      console.error('Biometric login error:', error);
      Alert.alert('Error', 'Biometric authentication failed');
    }
  };

  const updateUser = async (userData: Partial<User>) => {
    try {
      if (user) {
        const updatedUser = { ...user, ...userData };
        setUser(updatedUser);
        await storage.setItem('userData', JSON.stringify(updatedUser));
      }
    } catch (error) {
      console.error('Error updating user:', error);
      throw error;
    }
  };

  const value: AuthContextType = {
    user,
    isAuthenticated,
    isLoading,
    loading: isLoading, // Alias for backward compatibility
    login,
    register,
    logout,
    updateUser,
    enableBiometric,
    biometricLogin,
    isBiometricEnabled,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};